#name {
  position: fixed;
  z-index: 1;
  font-size: 80px;
  text-shadow: 2px 2px 2px black;
  left: 20px;
  color: white;
  -webkit-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease
}

#name.hidden {
  -webkit-transform: translateX(-680px);
      -ms-transform: translateX(-680px);
          transform: translateX(-680px);
  -webkit-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
}



